import utils, { randomColor, randomIntFromRange } from './utils'
import { noise } from '@chriscourses/perlin-noise'


const canvas = document.querySelector('canvas')
const c = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight


// Objects
class Circle {
  constructor(x, y, radius, color, offset) {
    this.x = x
    this.y = y
    this.radius = radius
    this.color = color
    this.offset = offset
  }

  draw() {
    c.beginPath()
    c.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    c.fillStyle = this.color
    c.fill()
    c.closePath()
  }

  update() {
    this.draw()
  }
}

const x = canvas.width / 2;
const y = canvas.height / 2;
const radius = 10;
const colors = ['blue','red','cyan','pink','purple','orange','lime']
//const color = 'blue';
// const circle = new Circle(x,y,radius,color)
const circles =[];
for (let i = 0; i < 10; i++) {
  circles.push(new Circle(x,y,radius,`hsl(${Math.random()*255},50%,50%)`, i*0.01));
  
}
let time = 0;

// Animation Loop
function animate() {
  requestAnimationFrame(animate)
  //c.clearRect(0, 0, canvas.width, canvas.height)
  c.fillStyle ='rgba(255,255,255,0.2)' 
  c.fillRect(0, 0, canvas.width, canvas.height)

  // Random effect en movimiento
  // Ruido entre 0 y 1 * medidas = random x,y dentro del canvas
  circles.forEach(circle => {
    circle.x = noise(time + 20 + circle.offset) * canvas.width;
    circle.y = noise(time + circle.offset) * canvas.height;
    time += 0.0005;
    circle.update();
  });

}

animate()
